.button-wrapper {
  max-width: 300px;
  background-color: #afc1bf;
  border: 5px solid rgba(36, 7, 7, .5);
  border-radius: 10px;
  justify-content: center;
  gap: 5px;
  margin: 120px auto 0;
  padding: 10px;
  display: flex;
}

.button-wrapper button {
  width: 100%;
  text-transform: uppercase;
  border-radius: 5px;
  padding: 20px;
  line-height: 1;
}

